"use client"

/**
 * FinSphere Frontend API Client
 * Handles all API calls to the backend
 */

const API_BASE_URL = typeof process !== "undefined" && process.env ? process.env.NEXT_PUBLIC_API_URL || "http://localhost:8000/api/v1" : "http://localhost:8000/api/v1"

// Helper function for API calls
async function apiCall<T>(
  endpoint: string,
  options: RequestInit = {}
): Promise<T> {
  const url = `${API_BASE_URL}${endpoint}`
  
  const response = await fetch(url, {
    headers: {
      "Content-Type": "application/json",
      ...options.headers,
    },
    ...options,
  })

  if (!response.ok) {
    const errorData = await response.json().catch(() => ({}))
    throw new Error(errorData.detail || `API Error: ${response.status}`)
  }

  return response.json()
}

// Types
export interface DashboardStats {
  stress_level: "Low" | "Medium" | "High"
  stress_score: number
  spending_risk: "Safe" | "Warning" | "Critical"
  cognitive_load: string
  savings_runway: string
  recent_interventions: Array<{
    time: string
    action: string
    reason: string
  }>
}

export interface BiometricData {
  user_id: string
  timestamp: string
  heart_rate: number
  hrv_ms: number
  source: string
  metadata?: Record<string, unknown>
}

export interface StressAssessment {
  user_id: string
  timestamp: string
  is_stressed: boolean
  stress_score: number
  trigger_event?: string
}

export interface MessageInput {
  user_id: string
  timestamp?: string
  channel: string
  sender: string
  content: string
}

export interface SentimentResult {
  score: number
  label: "positive" | "negative" | "neutral"
  risk_flag: boolean
}

export interface TransactionInput {
  user_id: string
  timestamp?: string
  amount: number
  currency?: string
  merchant: string
  category?: string
}

export interface InterventionResponse {
  should_intervene: boolean
  intervention_type?: "overlay" | "nudge" | "block"
  message?: string
  delay_minutes?: number
}

export interface InterventionLog {
  id: string
  timestamp: string
  url: string
  reason: string
  severity: "low" | "medium" | "high"
}

export interface InterventionListResponse {
  interventions: InterventionLog[]
  total_count: number
  date: string
}

export interface TherapyMessage {
  user_id: string
  message: string
}

export interface TherapyResponse {
  response_text: string
  audio_url?: string
}

// API Methods

/**
 * Get dashboard statistics for a user
 */
export async function getDashboardStats(userId: string): Promise<DashboardStats> {
  return apiCall<DashboardStats>(`/dashboard/${userId}`)
}

/**
 * Ingest biometric data
 */
export async function ingestBiometrics(data: BiometricData): Promise<StressAssessment> {
  return apiCall<StressAssessment>("/ingest/biometrics", {
    method: "POST",
    body: JSON.stringify(data),
  })
}

/**
 * Ingest a message for sentiment analysis
 */
export async function ingestMessage(msg: MessageInput): Promise<SentimentResult> {
  return apiCall<SentimentResult>("/ingest/message", {
    method: "POST",
    body: JSON.stringify(msg),
  })
}

/**
 * Ingest a transaction
 */
export async function ingestTransaction(txn: TransactionInput): Promise<{ status: string; id: string }> {
  return apiCall<{ status: string; id: string }>("/ingest/transaction", {
    method: "POST",
    body: JSON.stringify(txn),
  })
}

/**
 * Check if intervention should be triggered
 */
export async function checkIntervention(req: {
  user_id: string
  context_url?: string
  current_activity: string
}): Promise<InterventionResponse> {
  return apiCall<InterventionResponse>("/intervention/check", {
    method: "POST",
    body: JSON.stringify(req),
  })
}

/**
 * Log an intervention event
 */
export async function logIntervention(intervention: {
  user_id: string
  url: string
  reason: string
  severity: "low" | "medium" | "high"
}): Promise<{ status: string; id: string; timestamp: string }> {
  return apiCall<{ status: string; id: string; timestamp: string }>("/intervention/log", {
    method: "POST",
    body: JSON.stringify(intervention),
  })
}

/**
 * Get recent interventions for a user
 */
export async function getInterventions(
  userId: string,
  limit: number = 10
): Promise<InterventionListResponse> {
  return apiCall<InterventionListResponse>(`/interventions/${userId}?limit=${limit}`)
}

/**
 * Chat with the therapy mode
 */
export async function therapyChat(msg: TherapyMessage): Promise<TherapyResponse> {
  return apiCall<TherapyResponse>("/therapy/chat", {
    method: "POST",
    body: JSON.stringify(msg),
  })
}

/**
 * Health check
 */
export async function healthCheck(): Promise<{ status: string; service: string; version: string }> {
  return apiCall<{ status: string; service: string; version: string }>("/health")
}
